.slick-carousel-slider {
  background: #000000; }
  .slick-carousel-slider #slider {
    background: white; }
    .slick-carousel-slider #slider .slider-header {
      font-family: "Patron", "Open Sans", Helvetica, Arial, sans-serif;
      font-weight: bold;
      line-height: 1.1; }
    @media (max-width: 64em) {
      .slick-carousel-slider #slider .slide-container {
        margin-top: 5em; }
        .slick-carousel-slider #slider .slide-container:first-child {
          margin-top: 0; } }
    .slick-carousel-slider #slider .slide-text {
      max-width: 360px;
      margin-left: auto;
      margin-right: auto; }
      @media (max-width: 64em) {
        .slick-carousel-slider #slider .slide-text {
          max-width: 65%; } }
    .slick-carousel-slider #slider img {
      margin-bottom: 1em;
      margin-left: auto;
      margin-right: auto; }
    .slick-carousel-slider #slider .slide-block-set-title {
      font-family: "Patron", "Open Sans", Helvetica, Arial, sans-serif;
      font-weight: bold;
      line-height: 1.1;
      font-size: 20px; }
    .slick-carousel-slider #slider .slick-dots {
      position: relative;
      margin-top: 70px; }
      .slick-carousel-slider #slider .slick-dots li {
        height: 15px;
        width: 15px; }
        .slick-carousel-slider #slider .slick-dots li button {
          opacity: 1; }
          .slick-carousel-slider #slider .slick-dots li button:before {
            background: transparent;
            border: 1px solid #000;
            width: 100%;
            height: 100%;
            border-radius: 50%; }
        .slick-carousel-slider #slider .slick-dots li.slick-active button:before {
          background: #000;
          width: 100%;
          height: 100%;
          border-radius: 50%; }

html.magnifying > body {
  overflow-x: hidden !important; }
  html.magnifying > body .slick-list {
    overflow: visible !important; }

.magnify,
.magnify > .magnify-lens,
.magnify-mobile,
.lens-mobile {
  /* Reset */
  min-width: 0;
  min-height: 0;
  animation: none;
  border: none;
  float: none;
  margin: 0;
  opacity: 1;
  outline: none;
  overflow: visible;
  padding: 0;
  text-indent: 0;
  transform: none;
  transition: none; }

.magnify {
  position: relative;
  width: auto;
  height: auto;
  box-shadow: none;
  display: inline-block;
  z-index: inherit; }

.magnify > .magnify-lens {
  /* Magnifying lens */
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 100%;
  /* Multiple box shadows to achieve the glass lens effect */
  box-shadow: 0 0 0 7px rgba(255, 255, 255, 0.85), 0 0 7px 7px rgba(0, 0, 0, 0.25), inset 0 0 40px 2px rgba(0, 0, 0, 0.25);
  cursor: none;
  display: none;
  z-index: 99; }

.magnify > .magnify-lens.loading {
  background: #333 !important;
  opacity: 0.8; }

.magnify > .magnify-lens.loading:after {
  /* Loading text */
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  color: #fff;
  content: 'Loading...';
  font: italic normal 16px/1 Calibri, sans-serif;
  letter-spacing: 1px;
  margin-top: -8px;
  text-align: center;
  text-shadow: 0 0 2px rgba(51, 51, 51, 0.8);
  text-transform: none; }
